import React, { useEffect, useState } from 'react'
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import Box from '@mui/material/Box';
import styles from './profilePage'
import classNames from "classnames";
import { IconButton, InputAdornment, makeStyles, TextField } from '@material-ui/core';
import Dropzone from 'react-dropzone';
import { Card} from "react-bootstrap";
import axios from 'axios';
import { toast } from 'react-toastify';
import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';
import { styled } from '@mui/material/styles';
import profilePic from './assets/Grid/2103 087.jpg';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import './style.css'

const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
  height: 10,
  borderRadius: 5,
  [`&.${linearProgressClasses.colorSuccess}`]: {
    backgroundColor: theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800],
  },
  [`& .${linearProgressClasses.bar}`]: {
    borderRadius: 5,
    backgroundColor: theme.palette.mode === 'light' ? 'green' : '#308fe8',
  },
}));
const useStyles = makeStyles(styles);

const buttons = [

  <Button className='bg-warning'key="two"><i class="fa fa-thumbs-up text-light" aria-hidden="true"></i></Button>,
  <Button className='bg-primary text-light'key="three">300</Button>,
  <Button className='bg-secondary text-light'key="three">20</Button>,
  <Button className='bg-success text-light'key="three">482</Button>,
  <Button className='bg-info text-light'key="three">1K+</Button>,
  
];
const buttons2 = [
  <Button className='bg-danger text-light rounded' title="Add to Favourite"><IconButton size="large" aria-label="show 4 new mails" color="inherit">

    <FavoriteBorderIcon  />
  
</IconButton></Button>
 
];
export default function Profile() {
  const classes = useStyles();
  const [selectedImageForSearch, setSelectedForSearch] = React.useState({ file: '', imagePreviewUrl: '' });
  

  useEffect(() => {
  }, [])

  


  const handleClose = () => {
    clearValues()
  };
  const clearValues = () => {
    setSelectedForSearch({ file: '', imagePreviewUrl: '' })
  }
  const imageClasses = classNames(
    classes.imgRaised,
    classes.imgRoundedCircle,
    classes.imgFluid
  );


  // Dropzone Save file
  const onDropSearch = (files) => {
    let reader = new FileReader();
    let file = files[0];
    reader.onloadend = () => {
      setSelectedForSearch({
        file: file,
        imagePreviewUrl: reader.result
      });
    }

    reader.readAsDataURL(file)

  };

  // sample API Call
  const saveUserImage = () => {

    const formData = new FormData();
    formData.append('file', selectedImageForSearch.file)
    axios({
      method: "put",
      url: process.env.REACT_APP_API_URL + '/service/demoservice',
      data: formData,
      headers: { "Content-Type": "multipart/form-data",},
    }).then(response => {
      if (response.data.status) {
        toast.success("Profile Picture Updated Successfully !")
        window.location.reload()
        setSelectedForSearch({ imagePreviewUrl: '' })
        setTimeout(function () { handleClose() }, 2000);
      } else {
        toast.success("Something Went Wrong")
      }
    }

    )

  }
  return (
    <div >
      <div className=' d-flex justify-content-center'>
        <Card className='d-flex rounded-corners profile-details card-color' style={{ width: '40rem' }}>
          <div className='d-flex justify-content-between'>
            
          <Box
          className='mt-5'
      sx={{
        display: 'flex',
        '& > *': {
          m: 1,
        },
      }}
    >
      <ButtonGroup
        orientation="vertical"
        aria-label="vertical outlined button group"
      >
        {buttons}
      </ButtonGroup>
      </Box>
          <Dropzone onDrop={acceptedFiles => onDropSearch(acceptedFiles)}>
            {({ getRootProps, getInputProps }) => (
              <section className=" mt-2">
                <div {...getRootProps({ className: 'imgDropZone' })}>
                  <input {...getInputProps()} />
                  {selectedImageForSearch.imagePreviewUrl !== '' ?
                    <img className={imageClasses} src={selectedImageForSearch.imagePreviewUrl} alt='' style={{ width: '223px', height: '223px' }} /> :
                    <img className={imageClasses} src={profilePic} alt="..." style={{ width: '223px', height: '223px' }} />}
                </div>
                <aside>
                  <ul></ul>
                </aside>
              </section>
            )}
          </Dropzone>
          
          <Box
          className='mt-5'
      sx={{
        display: 'flex',
        '& > *': {
          m: 1,
        },
      }}
    >
      <ButtonGroup
        orientation="vertical"
        aria-label="vertical outlined button group"
      >
        {buttons2}
      </ButtonGroup>
      </Box>
          </div>
          {selectedImageForSearch.imagePreviewUrl !== '' ?
            <div className='d-flex justify-content-center mt-3'><h6 type='button' className='text-primary d-inline' onClick={() => { saveUserImage() }}>Save</h6>
              <h6 type='button' className='text-secondary d-inline ml-3' onClick={() => setSelectedForSearch({ imagePreviewUrl: '' })}>Cancel</h6></div>
            : ""}
          <Card.Body >
            <Card.Text>
              <div className="text-center">
                <h6 className='d-inline text-center '>ANGUNATHAN K</h6>
                <i class="fa fa-pencil-square-o ml-3 d-inline justify-content-end edit-profile" title="Edit Profile" aria-hidden="true" ></i>
                 <BorderLinearProgress variant="determinate" value={50} />
                
              </div>
              <div className='row'>
              <div className='col-12 text-left bold-text'>Basic Information <i class="fa fa-pencil-square-o pull-right mt-2"/></div>
                <div className='col-6 text-left bold-text'>Location:<p  className='light-text'>Sivagangai,Tamilnadu</p></div>
                <div className='col-6 text-left bold-text'>Mobile:<p  className='light-text'>+91 9994139531</p></div>
                <div className='col-6 text-left bold-text'>Gender:<p  className='light-text'>Male</p></div>
                <div className='col-6 text-left bold-text'>Birthday:<p  className='light-text'>16/07/1998</p></div>
                <div className='col-6 text-left bold-text'>Status:<p  className='light-text'>Single</p></div>
                <div className='col-6 text-left bold-text'>Age:<p  className='light-text'>23</p></div>
                <div className='col-6 text-left bold-text'>Looking For:<p  className='light-text'>Women</p></div>
                <div className='col-6 text-left bold-text'>Email:<p  className='light-text'>contactme@gmail.com</p></div>
                <div className='col-12 text-left bold-text'>Physical Apperance<i class="fa fa-pencil-square-o pull-right mt-2"/></div>
                <div className='col-6 text-left bold-text'>Height:<p  className='light-text'>5.6</p></div>
                <div className='col-6 text-left bold-text'>Hair Color:<p  className='light-text'>Black</p></div>
                <div className='col-6 text-left bold-text'>Weight:<p  className='light-text'>56</p></div>
                <div className='col-6 text-left bold-text'>Eye Color:<p  className='light-text'>Brown</p></div>
                <div className='col-12 text-left bold-text'>About Me<i class="fa fa-pencil-square-o pull-right mt-2"/></div>
                <div className='col-12 text-left'>Software Developer Currently Lookin Job Change</div>
                <div className='col-6 text-left mt-2 bold-text'>Current Level: <span className='bg-danger text-light rounded'>12</span></div>
                <div className='col-6 text-left mt-2 bold-text'>Current Level: <span className='bg-success text-light rounded'>5</span></div>
               
              </div>
              
           
              
            </Card.Text>
          </Card.Body>
        </Card>
      </div>
    </div>
  )
}