
import React from 'react'
import Box from '@mui/material/Box';
import SpinWheel from '../luckyspinner/spinner'
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Profile from '../editProfile/Profile'
import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';
import { styled } from '@mui/material/styles';
import { height } from '@mui/system';
const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
  height: 10,
  borderRadius: 5,
  [`&.${linearProgressClasses.colorSuccess}`]: {
    backgroundColor: theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800],
  },
  [`& .${linearProgressClasses.bar}`]: {
    borderRadius: 5,
    backgroundColor: theme.palette.mode === 'light' ? 'green' : '#308fe8',
  },
}));

class Client extends React.Component {
constructor(props){
    super(props)
    this.state={
      activeContent:'profile',
    }
}

setActiveContent=value=>()=>{
this.setState({activeContent: value})
  console.log(  value );
}
  render () {
   
    return (
        <div>
                        
                       
                          <React.Fragment>
      <Box className='p-5 ml-5 mr-5 mt-3 round card-color' sx={{borderRadius:'15px',height:'3rem',display: 'flex', alignItems: 'center', textAlign: 'center',    background: "white", }}>
        <h5 className={`mr-5 cursorpointer ${this.state.activeContent==='profile'?'activeContent':''}`} onClick={this.setActiveContent('profile')} sx={{ minWidth: 100, }} >Profile</h5>
        <h5  className={`mr-5 cursorpointer ${this.state.activeContent==='spin'?'activeContent':''}`} onClick={this.setActiveContent('spin')}sx={{ minWidth: 100 }}>Spin The Wheel</h5>
        <h5  className={`mr-5 cursorpointer ${this.state.activeContent==='gallery'?'activeContent':''}`} onClick={this.setActiveContent('gallery')} sx={{ minWidth: 100 }}>My Gallery</h5>
        <h5   className={`mr-5 cursorpointer ${this.state.activeContent==='favourite'?'activeContent':''}`} onClick={this.setActiveContent('favourite')} sx={{ minWidth: 100 }}>My Favorites</h5>
        <h5  className={`mr-5 cursorpointer ${this.state.activeContent==='member'?'activeContent':''}`} onClick={this.setActiveContent('member')} sx={{ minWidth: 100 }}>Member History</h5>
        <h5  className={`mr-5 cursorpointer ${this.state.activeContent==='user'?'activeContent':''}`} onClick={this.setActiveContent('user')} sx={{ minWidth: 100 }}>User Visitors</h5>
      
      </Box>
    </React.Fragment>
    {this.state.activeContent==='profile' ?<><div className='row p-5 m-0 d-flex justify-content-between'>
      <div className='col-lg-4 p-0 col-sm-4 col-md-4 '>
     
    <Profile/>
    
      </div>
      <div className='col-lg-8  pr-0 col-sm-8 col-md-8 '>
      <Card  className='card-color' variant="outlined" sx={{ minWidth: 275 }}>
      <CardContent>
     <SpinWheel/>
     </CardContent>
     </Card>
      </div>
    </div>
    <React.Fragment>
      <Box className='p-5 ml-5 mr-5 round mb-5 card-color' sx={{borderRadius:'15px',height:'3rem',display: 'flex', alignItems: 'center', textAlign: 'center',    background: "white" }}>
<div className='p-5 d-flex justify-content-between'>
  <h6 className='cursorpointer bold-text'>Current Meassage &nbsp; <span className='bg-danger text-light rounded'>7</span></h6>
  <h6 className='ml-5 mr-5 cursorpointer bold-text'>Your Chest &nbsp; <span className='bg-danger text-light rounded'>8.44</span></h6>
  <h6 className='ml-5 mr-5 cursorpointer bold-text'>Current Level &nbsp; <span className='bg-danger text-light rounded' style={{minWidth:"20px"}}>12</span></h6>
  <h6 className='ml-5 mr-5 cursorpointer bold-text'>Progress &nbsp; <span>   <BorderLinearProgress variant="determinate" value={50} /></span></h6>
  <h6 className='ml-5 mr-5 cursorpointer bold-text'>Coins  &nbsp;<span className='bg-warning text-light rounded'>55</span></h6></div>     
      </Box>
    </React.Fragment>
</>:<h1 className='mt-3' style={{height:"300px"}}>{this.state.activeContent.toLocaleUpperCase() + " is Not Available"}</h1>}</div>
    )
  }
}
export default Client
